.card-container{ 
    margin-left: 30px; 
    width: 300px; 
    overflow: hidden; 
    box-shadow: 0px 0px 15px -5px; 
    transition: 0.3s; 
    -webkit-animation: ease-in; 
            animation: ease-in; 
    background-color: "white"; 
    border-radius: 4%;
    background: #FFAFBD;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ffc3a0, #FFAFBD); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    

    
} 
 
.card-container:hover{ 
    text-align : center;
    -webkit-transform: scale(1.1);
